import compileBlock from '../compileBlock';
import insertBlock from '../insertBlock';

import chatsTemplate from './chats.tmpl';
import chatsList from './chatsList/chatsList';
import messages from './messages/messages';
import chatForm from './chatForm/chatForm';

const CHATS_LIST_ID = 'chats-list'
const MESSAGES_ID = 'messages';
const FORM_ID = 'form';

const result = compileBlock(chatsTemplate, {
    chatsListId: CHATS_LIST_ID,
    messagesId: MESSAGES_ID,
    formId: FORM_ID,
});

insertBlock(result, 'result');

const message1 = {
    text: `     ,    .  ,
             ,    ,  ,  
        ,    ,     .`,
    date: '',
    isMe: true,
    unread: false,
}

const message2 = {
    text: ``,
    date: '',
    isMe: false,
    unread: false,
}

const message3 = {
    text: ` ,    ,
                  ,       ,  
        ,    ,  .      ,   ,
          ,        ,    ,    , 
         ,   .`,
    date: '',
    isMe: false,
    unread: false,
}

const chat = {
    userName: '',
    unreadCount: 6,
    messages: [message1, message2, message3],
}
const chats = [ chat, chat ];

const chatsListCompiled = chatsList(chats);
insertBlock(chatsListCompiled, CHATS_LIST_ID);

const messagesCompiled = messages(chats[0]);
insertBlock(messagesCompiled, MESSAGES_ID);

const chatFormCompiled = chatForm('!');
insertBlock(chatFormCompiled, FORM_ID);

export type Message = {
    text: string;
    photo?: string;
    date: string;
    isMe: boolean;
    unread: boolean;
}

export type Chat = {
    userName: string;
    avatar?: string;
    messages: Array<Message>;
    unreadCount: number;
}

